










import Vue from "vue";

export default Vue.extend({
  name: "Index",
  mounted: function() {
    setTimeout(() => {
      if (parseInt(localStorage.visitCount) > 1) {
        this.$router.push({ path: "home" });
      } else {
        localStorage.visitCount = 0;
        this.$router.push({ path: "guidance" });
      }
      localStorage.visitCount++;
    }, 1000);
  }
});
